.texto-sobre-reparto h1 {
  margin-bottom: 1rem;
  color: #062c64;
  font-size: 4rem;
}
.texto-sobre-reparto p {
  color: #000;
  font-size: 2.5rem;
}
.imagen-sobre-reparto {
  width: 500px;
  margin-left: 30vh;
  height: auto;
  border-radius: 10px;
}
@media screen and (max-width: 1460px) {
  .texto-sobre-reparto h1 {
    font-size: 3.2rem;
  }
  .texto-sobre-reparto p {
    font-size: 2rem;
  }
  .imagen-sobre-reparto {
    width: 400px;
  }
}
@media screen and (max-width: 1200px) {
  .contenedor-info-sobre-reparto {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .texto-sobre-reparto h1 {
    font-size: 3.2rem;
    margin-bottom: 3rem;
  }
  .texto-sobre-reparto p {
    font-size: 2rem;
    margin-top: 1rem;
  }
  .imagen-sobre-reparto {
    margin-left: 0;
    margin-top: 5rem;
    width: 300px;
  }
}
@media screen and (max-width: 500px) {
  .texto-sobre-reparto h1 {
    font-size: 3rem;
    margin-bottom: 3rem;
  }
  .texto-sobre-reparto p {
    font-size: 1.6rem;
    margin-top: 1rem;
  }
}
.footer {
  width: 100%;
  height: 35vh;
  background-color: #062c64;
}
.contenedor-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.contenedor-footer p {
  color: #fff;
  font-weight: 400;
}
.contenedor-footer p a {
  color: #0d6efd;
}
.contenedor-footer .imagen-footer {
  width: 15rem;
  height: 15rem;
}
.redes {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.redes .redes__link {
  color: #fff;
  font-size: 2rem;
  margin: 1rem;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
  width: 5rem;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition:
    background-color 0.3s,
    color 0.3s;
  text-align: center;
}
.redes .redes__link:hover {
  color: blue;
  background-color: rgba(255, 255, 255, 0.3);
}
.redes .redes__link i {
  font-size: 2rem;
  text-align: center;
} 
